body {
font-family: 'Righteous', cursive;
color: #4169e1;
line-height: 1.7;
font-size: 19px;
background-color: #222222;
}

.nasa-photo {
display: flex;
padding: 40px;
margin: 0 auto;
max-height: 100%;
}

.nasa-photo img {
width: 50%;
margin-right: 0;
object-fit: contain;
max-height: 80vh;

}

@media screen and (min-width:1280px) {
.nasa-photo {
display: block;
}
.nasa-photo img {
width: 100%;
margin-right: 0;
margin-bottom: 30px;
}
}
.nasa-photo .explanation {
    color: #bbb;
}

.nasa-photo .date {
margin-top: 0;
margin-bottom: 0;
}

.home {
display: flex;
align-items: center;
justify-content: center;
height: 50vh;
}

.home-link {
font-size: 40px;
background: #191970;
display: inline-block;
padding: 40px 80px;
color: #f5f5f5;
border-radius: 50%;
text-decoration:none ;
transition: 0.3s ease all;
text-transform: uppercase;
}

.home-link:hover {
border-radius: 5px;
background: #1e90ff;
}







































.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



